 import lotusAddress from "@/components/Winglau14-lotusAddress/Winglau14-lotusAddress.vue";
export default{
	components:{
				 lotusAddress
	},
	data(){
		return{
			type:'add',
			uid:'',
			token:'',
			contactname:'',
			usermobile:'',
			address:'',
			addressid:'',
			url:'',
			lotusAddressData:{
			                visible:false,
			                provinceName:'',
			                cityName:'',
			                townName:'',
			            },
						isdefault:'2'
		}
	},
	onLoad(e){
		if(e.type=='edit'){
			this.type='edit'
			var item=JSON.parse(e.item)
			this.contactname=item.contactname
			this.usermobile=item.usermobile
			this.address=item.address
			this.lotusAddressData.provinceName=item.province
			this.lotusAddressData.cityName=item.city
			this.lotusAddressData.townName=item.district
			this.isdefault=item.isdefault
			this.addressid=item.id
			this.url='&r=api.member.address.edit'
		}else{
			this.url='&r=api.member.address.add'
		}
		var userInfo=uni.getStorageSync('userInfo')
		if(userInfo!='' & userInfo!=null & userInfo!=undefined){
			this.uid=userInfo.uid
			this.token=userInfo.token
		}
	},
	computed:{
		region:{
			 get(){
			     return `${this.lotusAddressData.provinceName} ${this.lotusAddressData.cityName} ${this.lotusAddressData.townName}`
			    },
			    set(val){}
		}
	},
	methods:{
		switch1Change(e){
			if(e.detail.value){
				this.isdefault='1'
			}else{
				this.isdefault='2'
			}
		},
		openPicker() {
		    this.lotusAddressData.visible = true;
		},
		//回传已选的省市区的值
		choseValue(res){
		    //res数据源包括已选省市区与省市区code
		    this.lotusAddressData.visible = res.visible;//visible为显示与关闭组件标识true显示false隐藏
		    //res.isChose = 1省市区已选 res.isChose = 0;未选
		    if(res.isChose){
		        this.lotusAddressData.provinceName = res.province;//省
		        this.lotusAddressData.cityName = res.city;//市
		        this.lotusAddressData.townName = res.town;//区
		        // this.region = `${res.province} ${res.city} ${res.town}`; //region为已选的省市区的值
		    }
		},
		submit(){
			var that=this
			var reg=/^1[3456789]\d{9}$/
			if(this.contactname==''){
				this.$msg('请输入姓名')
				return ;
			}
			if(!reg.test(this.usermobile)){
				this.$msg('请输入正确的手机号')
				return ;
			}
			if(this.lotusAddressData.provinceName==''){
				this.$msg('请选择所在地区')
				return ;
			}
			if(this.address==''){
				this.$msg('请输入详细地址')
				return ;
			}
			var params={
				uid:this.uid,
				token:this.token,
				contactname:this.contactname,
				usermobile:this.usermobile,
				province:this.lotusAddressData.provinceName,
				city:this.lotusAddressData.cityName,
				district:this.lotusAddressData.townName,
				address:this.address,
				isdefault:this.isdefault
			}
			if(this.type=='edit'){
				params.addressid=this.addressid
			}
			  this.$apiPost(this.url,params).then((res) =>{
				  if(that.type=='add'){
					  that.$msg('添加成功')
				  }else{
					  that.$msg('修改成功')
				  }
				  setTimeout(function(){
					  that.back()
					  uni.$emit('updateAddressList')
				  },1000)
			  })
		},
		back(){
			uni.navigateBack({
				delta:1
			})
		}
	}
}
